import Request from '@/common/luch-request/index.js'
import {
	ACCESS_TOKEN
} from '@/common/util/constants.js'
import configService from './config.service.js'
import tip from '@/common/util/tip.js';
import store from '@/store/index.js';
import {
	mapState,
	mapActions
} from 'vuex';

let apiUrl = configService.apiUrl;

const getTokenStorage = () => {
	let token = ''
	try {
		token = uni.getStorageSync(ACCESS_TOKEN)
		console.log("getTokenStorage1", token)
	} catch (e) {
		//TODO handle the exception
		console.log("getTokenStorage", token)
	}
	return token
}



const http = new Request()
http.setConfig((config) => {
	/*  */
	config.baseUrl = apiUrl /*  */
	config.header = {
		...config.header
	}
	return config
})

/**
 * true (resolve)(reject)
 * @param { Number } statusCode - statusCode
 * @return { Boolean } true, resolve,  reject
 */
// 
http.validateStatus = (statusCode) => {
	return statusCode === 200
}
// console.info("http::", http)
http.interceptor.request((config, cancel) => {
	/*  */
	config.header = {
		...config.header,
		'token': getTokenStorage()
	}
	/*
	if (!token) { // tokencancel catch() 
	  cancel('token ') // catch((err) => {}) err.errMsg === 'token '
	}
	*/
	return config
})

// 
http.interceptor.response(async (response) => {
	/*  */
	// if (response.data.code !== 200) { // 200reject()
	//   return Promise.reject(response)
	// }
	return response
}, (response) => {
	// 
	console.log("", response);
	if (response) {
		let data = response.data
		// const token = uni.getStorageSync(ACCESS_TOKEN)
		// console.log("------------", token)
		console.log("------------", data.status)
		switch (data.status) {
			case 403:
				tip.error('');
				break
			case 501:
				// let timeout = setTimeout(() => tip.error(''), 500);
				uni.showLoading({
					mask: true,
					title: ''
				})
				setTimeout(() => {
					uni.hideLoading();
					store.commit('SET_TOKEN', '')
					uni.setStorageSync("token", '');
					uni.switchTab({
						url: '/pages/index/index'
					})
				}, 800)
				break
			case 404:
				break
			case 503:
				tip.error(data.message);
				break
			case 401:
				break
			default:
				console.log(data.message)
				tip.error({
					duration: 0,
					forbidClick: true,
					message: data.message
				});
				break
		}
	}
	return response
})

export {
	http
}
